<template>
  <div class="farms">
    
    <div class="container">
      <div class="toolbar">
        <div class="toolbar-title">

          <span>Reward Pools<i style="color: #e91e61">(The R1 farming activity is suspend. R2 is coming soon!)</i></span>
            
        </div>


      </div>
      <div class="farm-items">


          <div class="farm-header">
              <div>Tokens</div>
              <div class="apy hidden-800">APY</div>
              <div class="assets">Total Staked</div>
              <div class="mydeposit hidden-800">My Staked</div>
          </div>
          <!--
          <v-expansion-panels focusable>
              <v-expansion-panel
                v-for="(item,i) in pools"
                :key="i"
             >
                <v-expansion-panel-header>
                  <div class="header-wrapper">
                    <div class="coin-icon">
                      <img height="40px" :src="item.image">
                    </div>
                    <div class="coin-info">
                      
                      <div class="coin" style="display:flex">
                        <div class="coin-icon">
                          <img height="40px" :src="item.image">
                        </div>
                        <div class="coin-name">{{ item.coin }}</div>
                      </div>
                      <div class="apy">{{ item.apy }}%</div>
                      <div class="assets">{{ item.totalAssets | toCurrency }}</div>
                      <div class="mydeposit">{{ item.myStake | toCurrency }}</div>
                    </div>
                  </div>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <div class="content-wrapper">
                    <div class="stake">
                      <div class="logo"><img height="40px" src="@/assets/img/usdc.svg"></div>
                      <div class="balance">0.0000</div>
                      <div class="info">Earned</div>
                      <vs-button border block class="harvest"> Harvest </vs-button>

                    </div>
                    <div class="withdraw">
                      <div class="logo"><img height="40px" :src="item.image">,</div>
                      <div class="balance">0.0000</div>
                      <div class="info">Token staked</div>
                      <vs-button gradient class="approve"> Approve </vs-button>
                      <div class="stake-unstake">
                        <vs-button gradient class="stakeop"> Stake </vs-button>
                        <vs-button border block class="unstakeop"> Unstake </vs-button>
                      </div>
                    </div>
                    
                  </div>
               </v-expansion-panel-content>
               
              </v-expansion-panel>
           </v-expansion-panels>
          
          -->
          <v-expansion-panels focusable>
              <v-expansion-panel
                v-for="(poolGoFarm, index) in poolGoFarms"
                :key="poolGoFarm.pid"
             >
                <v-expansion-panel-header>
                  <div class="header-wrapper">
                    <!--<div class="coin-icon">
                      <img height="40px" :src="item.image">
                    </div>-->
                    <div class="coin-info">
                      
                      <div class="coin" style="display:flex">
                        <div class="coin-icon">
                          <img height="40px" v-if="poolGoFarm.pid == '1'" src="@/assets/img/poolgo.svg">
                          <img height="40px" v-if="poolGoFarm.pid == '0'" src="@/assets/img/cfx.svg">
                          <img height="40px" v-if="poolGoFarm.pid == '2'" src="@/assets/img/poolgo-cfx.svg">
                          <img height="40px" v-if="poolGoFarm.pid == '3'" src="@/assets/img/poolgo-usdt.svg">
                        </div>
                        <div class="coin-name">{{poolGoFarm.wantTokenFullName}}</div>
                      </div>
                      <div class="apy hidden-800">{{ poolGoFarm.apyText }}</div>
                      <div class="assets">{{ toFixed(poolGoFarm.sharesTotal, 2)  }}--></div>
                      <div class="mydeposit hidden-800">{{ toFixed(poolGoFarm.wantLockedMine, 2)  }}</div>
                    </div>
                  </div>
                </v-expansion-panel-header>
                <v-expansion-panel-content>
                  <div class="content-wrapper">
                    <div class="stake">
                      <div class="logo">
                        <img height="40px" src="@/assets/img/poolgo.svg">
                      </div>
                      <div class="balance">0.0000<!--{{ poolGoFarm.pendingPoolGo.toFormat(4)}}--></div>
                      <div class="info">Earned</div>
                      <vs-button border block @click.stop="getReward(index)" class="harvest"> Harvest </vs-button>

                    </div>
                    <div class="withdraw">
                      <div class="logo">
                          <img height="40px" v-if="poolGoFarm.pid == '1'" src="@/assets/img/poolgo.svg">
                          <img height="40px" v-if="poolGoFarm.pid == '0'" src="@/assets/img/cfx.svg">
                          <img height="40px" v-if="poolGoFarm.pid == '2'" src="@/assets/img/poolgo-cfx.svg">
                          <img height="40px" v-if="poolGoFarm.pid == '3'" src="@/assets/img/poolgo-usdt.svg">
                      </div>
                      <div class="balance">{{ poolGoFarm.wantLockedMine.toFormat(4)}}</div>
                      <div class="info">Staked</div>
                      <!-- <vs-button gradient class="approve"> Approve </vs-button> -->
                      <div class="stake-unstake">
                        <vs-button gradient @click.stop="showStakeDialog(index)" class="stakeop"> Stake </vs-button>
                        <vs-button border block @click.stop="showUnStakeDialog(index)" class="unstakeop"> Unstake </vs-button>
                      </div>
                    </div>
                    
                  </div>
               </v-expansion-panel-content>
               
              </v-expansion-panel>
           </v-expansion-panels>


    <vs-dialog :loading="stakeDialogLoading" width="300px" not-center prevent-close v-model="stakeDialogShow">
      <template #header>
        <h4 class="not-margin">
          <b>{{$t('stakeToken')}}{{curPoolGoFarm.wantTokenFullName}} </b>
        </h4>
      </template>


      <div class="con-content">
        <el-row  v-if="isNeedApprove" style="font-size: 12px;">{{ $t('noticeApproveContent') }}</el-row>

        <vs-input  icon-after v-else v-model="stakeNumber" :placeholder="$t('noticeOwnToken')+wantTokenNumber.toFormat(2)" type="number">
          <template #icon>
            <vs-button @click="maxStakeNumber()" circle flat
                       warn
            >
              {{$t('max')}}
            </vs-button>
          </template>
        </vs-input>

      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button v-if="isNeedApprove" @click="confirmApprove" transparent>
            {{$t('noticeApprove')}}
          </vs-button>
          <vs-button v-else @click="confirmDeposit" transparent>
            {{$t('noticeConfirm')}}
          </vs-button>
          <vs-button @click="stakeDialogShow=false" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>


    <vs-dialog :loading="unStakeDialogLoading" width="300px" not-center prevent-close v-model="unStakeDialogShow">
      <template #header>
        <h4 class="not-margin">
          {{$t('unStakeTokenPart1')}} {{curPoolGoFarm.wantTokenFullName}}
          {{$t('unStakeTokenPart2')}}
        </h4>
      </template>


      <div class="con-content">

        <h5 class="not-margin">
          {{$t('unStakeLpTokenInfo')}}
        </h5>
      </div>

      <template #footer>
        <div class="con-footer">
          <vs-button @click="confirmUnStake" transparent>
            {{$t('noticeConfirm')}}
          </vs-button>
          <vs-button @click="unStakeDialogShow=false" dark transparent>
            {{$t('noticeCancel')}}
          </vs-button>
        </div>
      </template>
    </vs-dialog>
      </div>
    </div>

  </div>
</template>

<script>

import { 
  VIcon,
  VExpansionPanel, 
  VExpansionPanels, 
  VExpansionPanelHeader, 
  VExpansionPanelContent } from 'vuetify/lib'
import config from "@/config";
import {BigNumber} from "bignumber.js";
import {mapActions} from "vuex";
import {MAX_UINT256} from "@/contracts/Constants";


export default {
  name: "farm-view",
  components: {
    VIcon,
    VExpansionPanelContent,
    VExpansionPanelHeader,
    VExpansionPanel,
    VExpansionPanels
  },
  data() {
    return {
      value: '1',
      poolOpenIndex: 1, 
      activeName: 1,
      pools: [
        {
          "id": 0,
          "coin": "CFX",
          "image": require('@/assets/img/cfx.svg'),
          "apy": 3245,
          "totalAssets": 13432,
          "myStake": 24353
        },
        {
          "id": 1,
          "coin": "FC",
          "image": require('@/assets/img/fc.svg'),
          "apy": 3245,
          "totalAssets": 13432,
          "myStake": 24353
        },
      ],
      unStakeDialogShow: false,
      intervalTimer: null,
      curIndex:0,
      stakeDialogShow: false,
      stakeDialogLoading: false,
      unStakeDialogLoading: false,
      isNeedApprove: false,
      unStakeNum: '',
      wantTokenNumber: new BigNumber(0),
      stakeNumber:'',
      unStakeNumber:new BigNumber(0),
    };
  },
  computed: {
    poolGoFarms: function() {
      return this.$store.state.web3.poolGoFarms
    },
    poolGoFarmContract: function () {
      return this.$store.state.web3.poolGoFarmContract;
    },
    poolGoContract: function () {
      return this.$store.state.web3.poolGoContract;
    },
    account: function() {
      return this.$store.state.web3.account
    },
    curPoolGoFarm: function () {
      return this.poolGoFarms[this.curIndex];
    },
  },
  watch:{
    account: async function(newAcc, oldAcc) {
      this.intervalTimer = setInterval(this.update, 8000)
    }
  },
  async created() {
    var that = this

    console.log('---created---')
    that.update()
    that.intervalTimer = setInterval(that.update, 8000)
  },
  async mounted() {
    var that = this
  },
  beforeDestroy() {
    var that = this
    clearTimeout(that.intervalTimer)
  },
  methods: {
    ...mapActions(['cfxLogin']),

    async update() {
      const that = this

      //console.log('----update----')
      //console.log(that.poolGoFarms.length)
      for (let i = 0; i < that.poolGoFarms.length; i++) {
        setTimeout(async function(i) {
          //console.log('-----in update-----')

          let poolInfo = await that.poolGoFarmContract.poolInfo(that.poolGoFarms[i].pid)
          let poolGoPerSecond = await that.poolGoFarmContract.poolGoPerSecond()
          let totalAllocPoint = await that.poolGoFarmContract.totalAllocPoint()

          that.poolGoFarms[i].daliyPoolGo  = poolInfo.allocPoint.multipliedBy(poolGoPerSecond).div(totalAllocPoint).multipliedBy(60*60*24); 

          that.poolGoFarms[i].controllerFee = await that.poolGoFarms[i].strategyContract.controllerFee();

          that.poolGoFarms[i].sharesTotal = await that.poolGoFarms[i].strategyContract.sharesTotal();
          that.poolGoFarms[i].wantLockedTotal = await that.poolGoFarms[i].strategyContract.wantLockedTotal();


          if (i==0)
            that.poolGoFarms[i].apy = poolGoPerSecond.multipliedBy(60 * 60 * 24 * 365).multipliedBy(poolInfo.allocPoint)
                .div(totalAllocPoint.multipliedBy(that.poolGoFarms[i].wantLockedTotal)).multipliedBy(100);
          if (i==1) {
            that.poolGoFarms[i].apy = poolGoPerSecond.multipliedBy(60 * 60 * 24 * 365).multipliedBy(poolInfo.allocPoint)
                .multipliedBy(that.$store.state.web3.poolgoPrice).div(totalAllocPoint.multipliedBy(that.$store.state.web3.pcfxPrice).multipliedBy(that.poolGoFarms[i].wantLockedTotal)).multipliedBy(100);
          }
          if (i==2)
            that.poolGoFarms[i].apy = poolGoPerSecond.multipliedBy(60 * 60 * 24 * 365).multipliedBy(poolInfo.allocPoint)
                .multipliedBy(that.$store.state.web3.poolgoPrice).div(totalAllocPoint.multipliedBy((that.$store.state.web3.pcfxPrice.multipliedBy(that.$store.state.web3.poolgoPrice)).squareRoot().multipliedBy(2)).multipliedBy(that.poolGoFarms[i].wantLockedTotal)).multipliedBy(100);
          // if (i==3)
          //   that.poolGoFarms[i].apy = poolGoPerSecond.multipliedBy(60 * 60 * 24 * 365).multipliedBy(poolInfo.allocPoint)
          //   .multipliedBy(that.$store.state.web3.poolgoPrice).div(totalAllocPoint.multipliedBy(that.$store.state.web3.poolgoPrice.plus(1)).multipliedBy(that.poolGoFarms[i].wantLockedTotal)).multipliedBy(100);
          if (i==3)
            that.poolGoFarms[i].apy = poolGoPerSecond.multipliedBy(60 * 60 * 24 * 365).multipliedBy(poolInfo.allocPoint)
                .multipliedBy(that.$store.state.web3.poolgoPrice).div(totalAllocPoint.multipliedBy(that.$store.state.web3.poolgoPrice.squareRoot().multipliedBy(2)).multipliedBy(that.poolGoFarms[i].wantLockedTotal)).multipliedBy(100);

          if (that.poolGoFarms[i].isThirdFarm){
            that.poolGoFarms[i].apyText = that.poolGoFarms[i].apy.toFormat(2) + "%+4.08%"
          }else{
            that.poolGoFarms[i].apyText = that.poolGoFarms[i].apy.toFormat(2) + "%"
          }
          // console.log( that.poolGoFarms[i].apy.toFormat(2));

          if (that.$store.state.web3.account != null) {
            that.poolGoFarms[i].poolGoMine = await that.poolGoContract.balanceOf(that.$store.state.web3.account);
            let userInfo = await that.poolGoFarmContract.userInfo(that.poolGoFarms[i].pid,that.$store.state.web3.account );
            if(that.poolGoFarms[i].wantLockedTotal.gt(0)){
              that.poolGoFarms[i].wantLockedMine = userInfo.shares.div(that.poolGoFarms[i].sharesTotal).multipliedBy(that.poolGoFarms[i].wantLockedTotal);
            }
            // console.log("cToken "+await that.prizePoolContract.cToken());

            that.poolGoFarms[i].wantTokenNumber = await that.poolGoFarms[i].wantTokenContract.balanceOf(that.$store.state.web3.account);
            that.poolGoFarms[i].pendingPoolGo = await that.poolGoFarmContract.pendingPOOLGO(that.poolGoFarms[i].pid, that.$store.state.web3.account);

            // that.rewardNum = await that.stakePoolContract.earned(that.$store.state.web3.account);
            // that.lpTokenStakeNumber = await that.stakePoolContract.balanceOf(that.$store.state.web3.account);
          }
          
        }, i*1000, i)
      }
    },
    toFixed(number, fractionDigits) {
      var times = Math.pow(10, fractionDigits)
      var roundNum = Math.round(number * times) / times
      return roundNum.toFixed(fractionDigits)
    },
    async showUnStakeDialog(index) {
      var that = this;

      if (that.$store.state.web3.account == null) {
        await that.cfxLogin();
      }

      if (that.$store.state.web3.isPortalInstalled == false) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeWallet'),
        })
        return;
      }
      if (that.$store.state.web3.account == null) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeAddr'),
        })
        return ;
      }
      that.curIndex = index;
      that.unStakeDialogShow = true;

      // that.wantTokenNumber =  (await that.stakePoolContract.balanceOf(that.$store.state.web3.account));
    },
    async getReward(index){
      var that = this;
      let depositStatus = await that.poolGoFarmContract.deposit(that.$store.state.web3.account,that.poolGoFarms[index].pid, 0);
      if (depositStatus.succ == true) {

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece10'),
        })
        // this.showStakeDialog();
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: depositStatus.message,
        })

        return;
      }

    },
    async confirmUnStake() {
      let that = this;

      let index = that.curIndex;

      that.unStakeDialogLoading =true;
      let status = await that.poolGoFarmContract.withdraw(that.$store.state.web3.account,that.poolGoFarms[index].pid,that.poolGoFarms[index].wantLockedTotal);
      that.unStakeDialogLoading =false;
      console.log(status)
      if (status.succ == true) {

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece10'),
        })
        // this.showStakeDialog();
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: status.message,
        })

        return;
      }

      that.unStakeDialogShow = false;
      that.update();
    },
    async showStakeDialog(index) {
      var that = this;

      if (that.$store.state.web3.account == null) {
        await that.cfxLogin();
      }

      if (that.$store.state.web3.isPortalInstalled == false) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeWallet'),
        })
        return;
      }
      if (that.$store.state.web3.account == null) {
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('noticeAddr'),
        })
        return;
      }
      that.curIndex = index;
      that.stakeDialogShow = true;
      that.stakeDialogLoading = true;
      that.isNeedApprove = false;

      let curAllowance = await that.poolGoFarms[index].wantTokenContract.allowance(that.$store.state.web3.account, config.poolGoFarmAddress);
      if (curAllowance.isLessThan(10000000000)) {
        that.isNeedApprove = true;
      }
      that.wantTokenNumber = (await that.poolGoFarms[index].wantTokenContract.balanceOf(that.$store.state.web3.account));
      that.stakeDialogLoading = false;
      that.unStakeNum ='';
    },
    async confirmApprove(){
      let that = this;

      let index = that.curIndex;
      that.stakeDialogLoading = true;
      let curAllowance = await that.poolGoFarms[index].wantTokenContract.allowance(that.$store.state.web3.account, config.poolGoFarmAddress);
      if (curAllowance.isLessThan(10000000000)) {
        let status = await that.poolGoFarms[index].wantTokenContract.approveMax(that.$store.state.web3.account, config.poolGoFarmAddress);

        that.stakeDialogLoading = false;
        console.log(status)

        if (status.succ == true) {
          that.isNeedApprove = false;
        } else {

          this.$vs.notification({
            // width:'100%',
            duration: 10000,
            icon: "<i class='bx bx-error' ></i>",
            progress: 'auto',
            color: 'danger',
            position: 'top-center',
            title: that.$t('error'),
            text: status.message,
          })

          return;
        }
      }
      that.stakeDialogLoading = false;

    },
    async confirmDeposit() {
      let that = this;

      let index = that.curIndex;

      if (that.stakeNumber == '' || that.stakeNumber <=0){
        that.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: that.$t('correctNumPlaceholder'),
        })
        return;
      }
      that.stakeDialogLoading = true;

      let depositStatus = await that.poolGoFarmContract.deposit(this.$store.state.web3.account,that.poolGoFarms[index].pid, that.stakeNumber);

      that.stakeDialogLoading = false;
      console.log(depositStatus)
      if (depositStatus.succ == true) {
        that.stakeDialogShow = false;

        this.$vs.notification({
          // width:'100%',
          duration: 6000,
          icon: "<i class='bx bx-select-multiple' ></i>",
          progress: 'auto',
          color: 'success',
          position: 'top-center',
          title: that.$t('info'),
          text: that.$t('noticeSentece9'),
        })
      } else {
        this.$vs.notification({
          // width:'100%',
          duration: 10000,
          icon: "<i class='bx bx-error' ></i>",
          progress: 'auto',
          color: 'danger',
          position: 'top-center',
          title: that.$t('error'),
          text: depositStatus.message,
        })
        return;
      }

      that.update();
      // that.updateCfxAndPcfxBalance();
    },
    maxWithdrawPoolMoney(){
      let that = this;
      that.withdrawPoolMoney = that.ticketNumber.toFixed(2);
    },
    maxStakeNumber(){
      let that = this;
      that.stakeNumber = that.wantTokenNumber.toFixed(2);
      console.log(that.wantTokenNumber.toFixed(2))
    },
    formatPrice(value) {
        let val = (value/1).toFixed(2).replace('.', ',')
        return val.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ".")
    }
  },
};
</script>

<style>
.farms {
  padding-left: 20px;
  padding-right: 20px;
  margin-bottom: 260px;
}
.farms .container {
  margin: auto;
  max-width: 930px;
  background: #15171c;
  margin-top: 60px;
  border-radius: 5px;
  z-index: 100;
}
.container .toolbar {
  display: flex;
  justify-content: space-between;
  padding: 20px;
  border-bottom: 1px solid #e91e62;
}
.toolbar-title {
  display: flex;
}
.toolbar-title img {
  width: 40px;
  margin-right: 10px;
}
.toolbar-title span {
  line-height: 45px;
  margin-left: 10px;
  color: #aaa;
}
.vs-select {
  color: #00FF00;
  height: 45px;
  width: 100px;
}
.vs-select-content {
  width: 100px;
}
.vs-select.activeOptions .vs-select__input {
  background: #212327;
}
.vs-select__options {
  background: #212327;
}
.vs-select__input {
  height: 45px;
  background: #212327;
}
.vs-select__input:hover {
  background: #212327;
}
.vs-input {
  background: #212327;
  height: 45px;
  font-size: 20px;
}
.vs-input:focus {
  background: #000;
  border-color: #e91e62;
  border-width: 1px;
  color: #aaa;
}

.farm-items {
  margin-top: 40px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 50px;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  flex-wrap: wrap;
  min-height: 300px;

}

@media screen  and (max-width:600px) {
  .hidden-600 {
    display: none;
  }
}

.farm-collapsible {
  width: 100%;
}
.farm-collapsible .Collapsible__trigger {
}
.v-expansion-panels > *:first-child {
  background: #212327;
}
